import React from "react";

class ABC extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      userName: "",
      settime: false
    };
  }
  componentDidMount() {}
  apiCall = (param) => {
    console.log("api param", param);
  };

  handleChange = (e) => {
    this.setState({ userName: e.target.value });

    let test = setTimeout(() => {
      if (test) {
        this.apiCall(e.target.value);
      }
    }, 3000);
  };
  render() {
    return (
      <div>
        <input onChange={this.handleChange} value={this.state.userName} />
      </div>
    );
  }
}

export default ABC;
